function createIframe(id) {
    return `<iframe src="https://www.youtube.com/embed/${id}?rel=0&enablejsapi=1"  class="overlay__iframe"
    frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; 
    gyroscope; picture-in-picture" allowfullscreen></iframe>`
}



function showOverlay() {
    let overlay = document.querySelector(".overlay");
    const videoLink = document.querySelector(".video__link");
    let videoId = videoLink.getAttribute("href");
    let closeButton = document.querySelector('.overlay__close');
    const body = document.querySelector("body");
    function overlayShow() {
        setTimeout(function () {
            overlay.classList.toggle('overlay-show');
            setTimeout(function () {
                overlay.classList.toggle('overlay-done');
            }, 10);
        }, 10);
    }


    function overlayHide() {
        setTimeout(function () {
            overlay.classList.toggle('overlay-done');
            setTimeout(function () {
                overlay.classList.toggle('overlay-show');
            }, 600);
        }, 100);
    }

    videoLink.addEventListener("click", (el) => {
        el.preventDefault();
        overlayShow();
        body.classList.add("lock");
        if (overlay.lastElementChild.tagName !== "IFRAME") {
            overlay.insertAdjacentHTML("beforeend", createIframe(videoId));
            const iframe = document.querySelector('.overlay__iframe');
            if (window.innerWidth < window.innerHeight) {
                iframe.style.height = window.innerHeight / 3.5 + "px";
            } else {
                iframe.style.height = 70 + "%";
            }
        } else {
            overlay.classList.toggle("overlay-open");
        }

    })
    overlay.addEventListener("click", function toggle() {
        overlayHide();
        body.classList.remove("lock");
        const iframe = document.querySelector('.overlay__iframe');
        iframe.contentWindow.postMessage(JSON.stringify({
            event: 'command',
            func: 'pauseVideo'
        }), '*');
    })
    if (document.querySelector('.overlay__iframe')) {
        window.addEventListener("resize", () => {
            const iframe = document.querySelector('.overlay__iframe');
            if (window.innerWidth < window.innerHeight) {
                iframe.style.height = window.innerHeight / 3.5 + "px";
            } else {
                iframe.style.height = 70 + "%";
            }
        })
    }
}
showOverlay()

function shuffleLogo() {
    let array = ["../img/1.png", "../img/2.png", "../img/3.png", "../img/4.png", "../img/5.png", "../img/6.png", "../img/7.png", "../img/8.png", "../img/9.png", "../img/10.png", "../img/11.png", "../img/12.png", "../img/13.png"];
    let img = document.querySelectorAll(".logo__item");
    function shuffle() {
        let random = Math.floor(Math.random() * (array.length - 5));
        let randomImgId = img[random];
        randomImgId.style.opacity = 0;
        let randomNumber = Math.floor(Math.random() * ((array.length - 1) - (array.length - 5) + 1) + array.length - 5);
        setTimeout(() => {
            randomImgId.style.backgroundImage = `url('${array[randomNumber]}')`;
            randomImgId.style.opacity = 1;
            [array[random], array[randomNumber]] = [array[randomNumber], array[random]];
        }, 2000)
    }
    setInterval(shuffle, 3000)
}
shuffleLogo();

function burger() {
    let burger = document.querySelector(".burger");
    let list = document.querySelector(".nav__list");
    let navLink = document.querySelectorAll(".nav__link");
    burger.addEventListener("click", () => {
        burger.classList.toggle('burger-show');
        list.classList.toggle('nav__list-show');
        navLink.forEach((link, index) => {
            if (link.style.animation) {
                link.style.animation = "";
            } else {
                link.style.animation = `navLinkFade 1s ease forwards  ${index / 7 + 0.6}s`;
            }
        })
    })
    navLink.forEach(elem =>
        elem.addEventListener("click", () => {
            list.classList.remove("nav__list-show");
            burger.classList.remove("burger-show");
            navLink.forEach((link) => {
                if (link.style.animation) {
                    link.style.animation = "";
                }
            })
        }));
}

burger();


