function bubbles() {
    const section = document.querySelector(".features");
    const canvas = document.querySelector(".features__canvas");
    const ctx = canvas.getContext("2d");
    ctx.canvas.width = section.offsetWidth;
    ctx.canvas.height = section.offsetHeight;
    let particleCount = window.innerWidth <= 1148 ? 200 : 400;
    let particleArray = [];
    let color = ['#a18cd1', "#fbc2eb", "#667eea", "#a6c0fe", "#e0c3fc", "#00f2fe", "#764ba2", "#2575fc"];
    class Bubble {
        constructor() {
            this.size = 1;
            this.x = Math.random() * (section.offsetWidth - this.size * 2);
            this.y = Math.random() * (section.offsetHeight - this.size * 2);
            this.directionX = (Math.random() * 1) - 0.2;
            this.directionY = Math.random() * (-1.2 - -0.9) + -0.9;
            this.color = color[Math.floor(Math.random() * (color.length - 1))];
        }
        draw() {
            ctx.beginPath();
            ctx.arc(this.x, this.y, this.size, 0, Math.PI * 2, false);
            ctx.shadowColor = this.color;
            ctx.shadowBlur = 4;
            ctx.fillStyle = this.color;
            ctx.fill();
        }

        update() {
            if (this.x + this.size > canvas.width || this.x - this.size < 0) {
                this.x = Math.random() * (section.offsetWidth - this.size * 2);
                this.y = Math.random() * ((section.offsetHeight - this.size * 2) - (section.offsetHeight - 200)) + (section.offsetHeight - 200);
            }
            if (this.y + this.size > canvas.height || this.y - this.size < 0) {
                this.x = Math.random() * (section.offsetWidth - this.size * 2);
                this.y = Math.random() * ((section.offsetHeight - this.size * 2) - (section.offsetHeight - 200)) + (section.offsetHeight - 200);
            }

            this.x += this.directionX;
            this.y += this.directionY;
            this.draw();
        }
    }
    function init() {
        for (let i = 0; i < particleCount; i++) {
            particleArray.unshift(new Bubble);
        }
    }

    function animate() {
        requestAnimationFrame(animate);
        ctx.clearRect(0, 0, section.offsetWidth, section.offsetHeight);
        for (let i = 0; i < particleArray.length; i++) {
            particleArray[i].update();
        }
    }

    init();
    animate();

    window.addEventListener("resize", () => {
        canvas.width = section.offsetWidth;
        canvas.height = section.offsetHeight;
        particleCount = window.innerWidth <= 1148 ? 200 : 400;
        particleArray = [];
        init();
    })
}
bubbles()

function scrollBar() {
    let progress = document.querySelector(".progressbar")
    let totalHeight = document.body.scrollHeight - window.innerHeight;
    window.addEventListener("scroll", () => {
        let progressHeight = (window.pageYOffset / totalHeight) * 100;
        progress.style.height = progressHeight + "%";
    })
}
scrollBar()

